<template>
  <div class="home">
    <v-container>
      <v-row class="pa-1" justify="center" align="center">
        <v-col cols="10" align="left">
          <h2 class="">Calendar Meeting</h2>
        </v-col>
        <v-col cols="2" align="right">
          <v-icon class="mb-2" color="teal">mdi-calendar-month-outline</v-icon>
        </v-col>
      </v-row>
      <v-divider></v-divider>

      <v-row class="fill-height pt-2" justify="center" align="center">
        <v-col class="pl-3" cols="3" align="right">
          <v-btn fab text color="grey darken-2" @click="prev">
            <v-icon large color="indigo">
              mdi-menu-left
            </v-icon>
          </v-btn>
        </v-col>

        <v-col  cols="6" align="center">
          <v-toolbar-title v-if="$refs.calendar">
            {{ $refs.calendar.title }}
          </v-toolbar-title>
        </v-col>
        <v-col class="pr-3" cols="3" align="left">
          <v-btn fab text  color="grey darken-2" @click="next">
            <v-icon large color="indigo">
              mdi-menu-right
            </v-icon>
          </v-btn>
        </v-col>
      </v-row>

      <v-row class="fill-height pa-1" justify="center" align="center">
        <v-col class="pt-0" align="center">
          <v-sheet class="mb-2" height="64">
            <v-toolbar flat>
              <v-btn
                outlined
                
                class="mr-2"
                color="grey darken-2"
                @click="setToday"
              >
                Today
              </v-btn>
              
              <v-btn
                outlined
              
                class="mr-2"
                color="grey darken-2"
                @click="setWeek"
              >
                Week
              </v-btn>

              <v-btn
                outlined
            
                class="mr-2"
                color="grey darken-2"
                @click="setMonth"
              >
                Month
              </v-btn>

          <v-btn
                outlined
            
                class="mr-2"
                color="grey darken-2"
                @click="set4day"
              >
                4day
              </v-btn>
            </v-toolbar>
          </v-sheet>
          <v-sheet height="600">
            <v-calendar
              ref="calendar"
              v-model="focus"
            
              color="primary"
              :events="events"
              :event-color="getEventColor"
              :type="type"
        
              @click:more="viewDay"
              @click:date="viewDay"
              @change="updateRange"
     
              @click:day="addevent(date)"
            ></v-calendar>
            <v-menu
              v-model="selectedOpen"
              :close-on-content-click="false"
              :activator="selectedElement"
              offset-x
            >
              <v-card color="grey lighten-4" min-width="350px" flat>
                <v-toolbar :color="selectedEvent.color" dark>
                  <v-btn icon>
                    <v-icon>mdi-pencil</v-icon>
                  </v-btn>
                  <v-toolbar-title
                    v-html="selectedEvent.name"
                  ></v-toolbar-title>
                  <v-spacer></v-spacer>
                  <v-btn icon>
                    <v-icon>mdi-heart</v-icon>
                  </v-btn>
                  <v-btn icon>
                    <v-icon>mdi-dots-vertical</v-icon>
                  </v-btn>
                </v-toolbar>
                <v-card-text>
                  <span v-html="selectedEvent.details"></span>
                </v-card-text>
                <v-card-actions>
                  <v-btn text color="secondary" @click="selectedOpen = false">
                    Cancel
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-menu>
          </v-sheet>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import Swal from "sweetalert2";
export default {
  name: "HomePage",
  components: {},
  data: () => ({
    loading: false,
    user_login: "",
    focus: "",
    type: "month",
    selectedEvent: {},
    selectedElement: null,
    selectedOpen: false,
    events: [],
    colors: [
      "blue",
      "indigo",
      "deep-purple",
      "cyan",
      "green",
      "orange",
      "grey darken-1",
    ],
    names: [
   
    ],
  }),
  mounted() {
    this.$refs.calendar.checkChange();
  },
  computed: {},
  created() {
    this.get_user_login();
  },
  methods: {
    viewDay({ date }) {
      this.focus = date;
      this.type = "day";
    },
    getEventColor(event) {
      return event.color;
    },
    setToday() {
       console.log(this.type);
      this.type = "day"
    },
    setWeek(){
        console.log(this.type);
      this.type = "week"
    },
    setMonth(){
        console.log(this.type);
      this.type = "month"
    },
    set4day(){
        console.log(this.type);
      this.type = "4day"
    },
    prev() {
      this.$refs.calendar.prev();
    },
    next() {
      this.$refs.calendar.next();
    },
    addevent(){
     console.log("******");
       console.log(events);
    },
    updateRange({ start, end }) {
        // console.log(start)
       // console.log(end)
      const events = [];

      const min = new Date(`${start.date}T00:00:00`);
      const max = new Date(`${end.date}T23:59:59`);
      const days = (max.getTime() - min.getTime()) / 86400000;
      const eventCount = this.rnd(days, days + 20);

      for (let i = 0; i < eventCount; i++) {
        const allDay = this.rnd(0, 3) === 0;
        const firstTimestamp = this.rnd(min.getTime(), max.getTime());
        const first = new Date(firstTimestamp - (firstTimestamp % 900000));
        const secondTimestamp = this.rnd(2, allDay ? 288 : 8) * 900000;
        const second = new Date(first.getTime() + secondTimestamp);

        events.push({
          name: this.names[this.rnd(0, this.names.length - 1)],
          start: first,
          end: second,
          color: this.colors[this.rnd(0, this.colors.length - 1)],
          timed: !allDay,
        });
      }

      this.events = events;
    },
    rnd(a, b) {
      return Math.floor((b - a + 1) * Math.random()) + a;
    },
    get_user_login() {
      let data = JSON.parse(
        window.localStorage.getItem("user_login_oneaccess")
      );
      // console.log(data)
      if (data === null) {
        this.user_login = "";
      } else {
        this.user_login = data;
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.truncatecus {
  max-width: 420px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>
